body{
    margin: 0;
    padding: 10px;
    box-sizing: border-box;
    background-color: #343d46;
    color: #c0c5ce;
}  

.modal-content{
    background-color: #343d46; 
}

#response-textarea {
    background: transparent;
}

.form-control{ 
    background:#4f5b66 !important;  
    color: white !important;
}

#query-input-container{
    width: 100%;
}

#query-input{
    flex-grow: 1;
    border-radius: .25rem;
    background: #E9ECEF;
}

.text-style-options{
    display: flex;
    justify-content: flex-end;
}

.text-style-options button{
    background-color: #343d46;
    padding: .5em;
    color: white;
    border: none;
    border-radius: 2px;
}